.filters {
    padding: 1rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    
    .MuiTypography-root {
        font-weight: 600;
        font-size: small;
    }

    .filters-header {
        display: flex;
        align-items: center;
        gap: .5rem;

        & > .MuiTypography-root {
            font-size: medium;
        }
    }

    .slider-label {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

}